define('/kit/filters/trim', function(require, exports, module) {

  'use strict';
  
  /**
   * @file trim
   * @author zhujianchen
   * @description
   */
  window.Vue.filter('trim', {
      read: function read(value) {
          if (value) {
              return value.replace(/(^\s*)|(\s*$)/g, '');
          }
  
          return value;
      },
      write: function write(value) {
          if (value) {
              return value.replace(/(^\s*)|(\s*$)/g, '');
          }
  
          return value;
      }
  });

});
